@font-face {
  font-family: "Montserrat";
  src: url("/fonts/montserrat-regular.woff2") format("woff2"), url("/fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("/fonts/montserrat-medium.woff2") format("woff2"), url("/fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("/fonts/montserrat-semibold.woff2") format("woff2"), url("/fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("/fonts/montserrat-bold.woff2") format("woff2"), url("/fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Calibri", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #fdfbf9;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #fdfbf9;
  -webkit-text-fill-color: #011c40;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.menu-opened {
  overflow: hidden;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023.2px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767.2px) {
  .container {
    padding: 0 15px;
  }
}

.header .container,
.footer .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header .container {
  padding-top: 35px;
  padding-bottom: 35px;
}
@media (max-width: 1023.2px) {
  .header .container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
@media (max-width: 767.2px) {
  .header .container {
    padding-top: 20px;
    padding-bottom: 20px;
    flex-direction: column;
  }
}

.header--menu-opened .container {
  margin-right: 0;
  height: 100vh;
  width: calc(100% - 36px);
  justify-content: flex-start;
  background-color: #fdfbf9;
}

@media (max-width: 767.2px) {
  .header--no-js .container {
    margin-right: 0;
    justify-content: flex-start;
    background-color: #fdfbf9;
  }
}

.footer .container {
  margin-right: auto;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (max-width: 767.2px) {
  .footer .container {
    padding-top: 40px;
    padding-bottom: 40px;
  }
}

.about .container {
  display: flex;
  justify-content: space-between;
  gap: 80px;
}
@media (max-width: 1023.2px) {
  .about .container {
    align-items: flex-start;
    gap: 29px;
  }
}
@media (max-width: 767.2px) {
  .about .container {
    flex-direction: column;
    gap: 33px;
  }
}

.booking .container {
  display: grid;
  padding-left: 163px;
  padding-right: 163px;
  grid-template-columns: 1fr 403px;
  gap: 50px;
}
@media (max-width: 1023.2px) {
  .booking .container {
    padding-left: 174px;
    padding-right: 174px;
    grid-template-columns: 1fr;
    gap: 35px;
  }
}
@media (max-width: 767.2px) {
  .booking .container {
    padding-left: 15px;
    padding-right: 15px;
    gap: 17px;
  }
}

.about {
