.burger {
  display: none;
}
.header--no-js .burger {
  display: none;
}
@media (max-width: 767.2px) {
  .burger {
    position: absolute;
    display: inline-block;
    padding: 10px;
    right: 5px;
    box-sizing: border-box;
    border: none;
    background-color: transparent;
  }
}

.header--menu-opened .burger {
  right: 0;
}

.burger__cross {
  position: relative;
  display: block;
  width: 24px;
  height: 2px;
  background-color: #fdfbf9;
}
.burger__cross::after, .burger__cross::before {
  position: absolute;
  width: 24px;
  height: 2px;
  left: 0;
  background-color: #fdfbf9;
  content: "";
  transform: rotate(0deg);
  transition: transform 0.3s ease;
}
.burger__cross::after {
  top: 5px;
}
.burger__cross::before {
  bottom: 5px;
}

.header--menu-opened .burger__cross::after, .header--menu-opened .burger__cross::before {
  width: 19px;
  transform-origin: center right;
  background-color: #011c40;
}
.header--menu-opened .burger__cross::after {
  top: 7px;
  transform: rotate(45deg);
}
.header--menu-opened .burger__cross::before {
  bottom: 7px;
  transform: rotate(-45deg);
}

.custom-input {
  position: relative;
  display: flex;
}
.custom-input input {
  flex-grow: 1;
  margin-left: auto;
  padding: 12px 16px 13px;
  background-color: #fdfbf9;
  border: none;
  outline: none;
  transition: background-color 0.3s ease;
}
.custom-input input:hover {
  background-color: rgba(253, 251, 249, 0.5);
}
.custom-input input:focus {
  background-color: rgba(253, 251, 249, 0.8);
}
.custom-input input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
}
.custom-input__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}

.custom-toggle {
  display: flex;
  align-items: center;
}
.custom-toggle input {
  position: relative;
  display: inline-flex;
  width: 18px;
  height: 18px;
  order: 1;
  appearance: none;
  background-color: #fdfbf9;
}
.custom-toggle input:checked::after {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../img/svg/checkbox.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 60%;
  content: "";
}
.custom-toggle label {
  padding-left: 5px;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  order: 2;
  color: #fdfbf9;
  cursor: pointer;
}
.custom-toggle.is-invalid label span {
  color: #ff1553;
}
@media (max-width: 767.2px) {
  .custom-toggle input {
    width: 20px;
    height: 20px;
  }
  .custom-toggle label {
    padding-left: 8px;
    width: 70%;
  }
}

.footer {
  display: flex;
  background-color: #011c40;
}

.header {
  position: absolute;
  display: flex;
  width: 100%;
  z-index: 1;
}
.header--menu-opened {
  width: 100%;
  height: 100vh;
  background: rgba(1, 28, 64, 0.5);
}
.header--no-js {
  position: static;
}

.intro {
  display: flex;
  padding: 198px 95px;
  min-height: 650px;
  flex-direction: column;
  justify-content: center;
  background-color: #011c40;
  color: #fdfbf9;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-image: image-set(url("../img/backgrounds/hero.webp") 1x, url("../img/backgrounds/hero@2x.webp") 2x, url("../img/backgrounds/hero.jpg") 1x, url("../img/backgrounds/hero@2x.jpg") 2x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.intro h1 {
  margin: 0;
  padding-bottom: 15px;
  font-size: 54px;
  line-height: 66px;
}
.intro p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}
@media (min-width: 1024px) {
  .intro {
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
  }
}
@media (max-width: 1023.2px) {
  .intro {
    min-height: 638px;
    padding: 45px 0;
    background-image: image-set(url("../img/backgrounds/hero_tablet.webp") 1x, url("../img/backgrounds/hero_tablet@2x.webp") 2x, url("../img/backgrounds/hero_tablet.jpg") 1x, url("../img/backgrounds/hero_tablet@2x.jpg") 2x);
  }
  .intro h1 {
    font-size: 50px;
    line-height: 61px;
  }
  .intro p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767.2px) {
  .intro {
    min-height: 500px;
    background-image: image-set(url("../img/backgrounds/hero_mobile.webp") 1x, url("../img/backgrounds/hero_mobile@2x.webp") 2x, url("../img/backgrounds/hero_mobile.jpg") 1x, url("../img/backgrounds/hero_mobile@2x.jpg") 2x);
  }
  .intro h1 {
    padding-bottom: 10px;
    font-size: 36px;
    line-height: 45px;
  }
  .intro p {
    font-size: 16px;
    line-height: 22px;
  }
}

.logo {
  display: inline-block;
  margin-right: 40px;
  color: #fdfbf9;
}
@media (max-width: 1023.2px) {
  .logo {
    margin-right: 25px;
  }
}

@media (max-width: 767.2px) {
  .header--no-js .logo,
  .header--menu-opened .logo {
    color: #011c40;
  }
}

.logo__icon {
  width: 70px;
  height: 38px;
  fill: currentColor;
}
@media (max-width: 1023.2px) {
  .logo__icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767.2px) {
  .logo__icon {
    width: 42px;
    height: 22px;
  }
}

.navigation-list {
  display: flex;
  margin: 0;
  padding: 13px 0 0;
  height: fit-content;
  flex-wrap: wrap;
  list-style: none;
  gap: 33px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #fdfbf9;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
@media (max-width: 1023.2px) {
  .navigation-list {
    padding-top: 0;
    gap: 23px;
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767.2px) {
  .navigation-list {
    flex-direction: column;
    gap: 7px;
    font-size: 18px;
    line-height: 22px;
  }
}

@media (max-width: 767.2px) {
  .header .navigation-list {
    display: none;
  }
}

@media (max-width: 767.2px) {
  .header--no-js .navigation-list {
    display: flex;
    color: #011c40;
    filter: none;
  }
}

@media (max-width: 767.2px) {
  .header--menu-opened .navigation-list {
    display: flex;
    padding: 28px 0;
    color: #011c40;
    filter: none;
  }
}

.footer .navigation-list {
  margin-right: auto;
}
@media (max-width: 1023.2px) {
  .footer .navigation-list {
    font-size: 12px;
    line-height: 15px;
  }
}

.navigation-list__item {
  display: flex;
}

@media (max-width: 767.2px) {
  .navigation-list__link {
    padding: 10px;
  }
}

.social-list {
  display: flex;
  margin: 0 0 0 30px;
  padding: 0;
  flex-wrap: wrap;
  list-style: none;
  gap: 7px;
}
@media (max-width: 1023.2px) {
  .social-list {
    gap: 2px;
  }
}

.social-list__link {
  display: inline-block;
  padding: 10px;
  color: #fdfbf9;
}

.social-list__icon {
  height: 20px;
  fill: currentcolor;
}
@media (max-width: 1023.2px) {
  .social-list__icon {
    height: 20px;
  }
}

.tours {
  padding-top: 85px;
  padding-bottom: 120px;
}
.tours h2 {
  margin: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023.2px) {
  .tours {
    padding-top: 90px;
    padding-bottom: 100px;
  }
  .tours h2 {
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767.2px) {
  .tours {
    padding-top: 60px;
    padding-bottom: 65px;
  }
  .tours h2 {
    font-size: 24px;
    line-height: 34px;
  }
}

.tours__list {
  display: grid;
  margin: 0;
  padding: 49px 15px 0;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: 1fr;
  gap: 30px;
  list-style: none;
}
@media (max-width: 1279.2px) {
  .tours__list {
    padding-top: 52px;
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 767.2px) {
  .tours__list {
    padding-top: 30px;
    padding-left: 10px;
    padding-right: 10px;
    grid-template-columns: 1fr;
  }
}

.tours__list-item {
  display: flex;
  padding: 28px 25px 35px;
  flex-direction: column;
  background-color: #011c40;
  color: #fdfbf9;
}
.tours__list-item h3 {
  margin: 0;
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
}
@media (max-width: 1023.2px) {
  .tours__list-item {
    padding: 35px;
  }
}
@media (max-width: 767.2px) {
  .tours__list-item {
    padding: 28px 25px 35px;
  }
}

.tours__card-content {
  margin: 13px 0 0;
}
.tours__card-content dt,
.tours__card-content dd {
  font-size: 14px;
  line-height: 20px;
}
.tours__card-content dt {
  font-weight: 500;
}
.tours__card-content dd {
  margin-left: 0;
  padding-bottom: 11px;
  padding-top: 2px;
  font-weight: 400;
}

.tours__link {
  display: inline-block;
  margin-top: auto;
  padding: 10px 46px;
  width: 100%;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  background-color: #fdfbf9;
  color: #011c40;
}

