.advantages {
  padding-top: 110px;
  padding-bottom: 120px;
  background-image: image-set(url("../img/backgrounds/advantages.webp") 1x,
  url("../img/backgrounds/advantages@2x.webp") 2x,
  url("../img/backgrounds/advantages.jpg") 1x,
  url("../img/backgrounds/advantages@2x.jpg") 2x);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  h2 {
    margin: 0;
    font-weight: 600;
    font-size: 28px;
    line-height: 42px;
    text-align: center;
  }

  @include vp-1023 {
    padding-top: 90px;
    padding-bottom: 100px;
    background-image: image-set(url("../img/backgrounds/advantages_tablet.webp") 1x,
    url("../img/backgrounds/advantages_tablet@2x.webp") 2x,
    url("../img/backgrounds/advantages_tablet.jpg") 1x,
    url("../img/backgrounds/advantages_tablet@2x.jpg") 2x);

    h2 {
      font-size: 26px;
      line-height: 36px;
    }
  }

  @include vp-767 {
    padding-top: 60px;
    padding-bottom: 63px;
    background-image: image-set(url("../img/backgrounds/advantages_mobile.webp") 1x,
    url("../img/backgrounds/advantages_mobile@2x.webp") 2x,
    url("../img/backgrounds/advantages_mobile.jpg") 1x,
    url("../img/backgrounds/advantages_mobile@2x.jpg") 2x);

    h2 {
      font-size: 24px;
      line-height: 34px;
    }
  }
}

.advantages__list {
  display: grid;
  margin: 0;
  padding: 49px 0 0;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  list-style: none;

  @include vp-1023 {
    padding-top: 50px;
    gap: 30px;
  }

  @include vp-767 {
    padding-top: 28px;
    grid-template-columns: 1fr;
    grid-auto-rows: 1fr;
  }
}

.advantages__list-item {
  display: flex;
  padding: 30px 30px 45px;
  flex-direction: column;
  background-color: $color-seashell;

  h3 {
    margin: 0;
    padding-top: 1em;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;
  }

  p {
    margin: 0;
    padding-top: 0.7em;
    font-size: 14px;
    line-height: 20px;
  }

  @include vp-1023 {
    padding: 27px 15px 20px 20px;

    h3 {
      line-height: 25px;
    }
  }
}

.advantages__icon {
  fill: currentcolor;

  @include vp-1023 {
    margin-left: -5px;
    height: 26px;
  }
}
